@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.cabecera {
  background-color: black;
  padding: 1rem; }

.icono {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.container_menu {
  display: none; }

.logo {
  height: 3vh; }

.hamburguesa {
  height: 3vh; }

.footer {
  background-color: black;
  color: #fafafa;
  padding: 3rem 1.5rem; }

.pais-box {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 1rem; }

.pais {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: 1px solid lightblue; }

.spain {
  padding: 0.5rem; }

.enlaces {
  text-decoration: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  border: 1px solid red;
  margin-bottom: 1rem; }

.enlaces-der {
  border: 1px solid yellow;
  list-style: none;
  padding-top: 5px; }

.enlaces-izq {
  border: 1px solid yellow;
  display: flex;
  flex-direction: row;
  list-style: none;
  text-decoration: none;
  color: white; }

.legal {
  display: inline-block;
  padding: 5px;
  list-style: none;
  text-decoration: none;
  color: white; }

.cookies {
  display: inline-block;
  padding: 5px;
  list-style: none;
  text-decoration: none;
  color: white; }

.enlace-infor {
  border: 1px solid green;
  padding: 5px; }

.informacion {
  text-decoration: none;
  color: white; }

.h1 {
  font-size: 5rem;
  font-weight: bolder;
  text-align: left;
  padding-top: 10rem;
  line-height: 1.15; }

.main-1_h2 {
  font-size: 2.7rem;
  text-align: left;
  padding-top: 1.5rem;
  line-height: 1.5; }

.info {
  font-size: 1.5rem;
  line-height: 1.5;
  margin: 20px 0; }

.main {
  background-color: white;
  color: white; }

.main-1 {
  background-color: #3494dd;
  border: 2px solid green;
  padding: 2rem; }

.buttom {
  display: inline-block;
  border-radius: 2rem;
  background-color: green;
  padding: 2rem;
  display: flex;
  justify-content: center;
  margin: 1rem 0;
  color: white;
  text-decoration: none;
  font-size: 1.5rem; }

.main-2 {
  padding: 40px;
  background-color: white;
  border: 2px solid orange;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }

.h2 {
  margin-top: -20px;
  margin-bottom: 20px;
  padding: 10px;
  text-align: center;
  font-size: 4rem;
  line-height: 1.15;
  font-weight: bold; }

.h3 {
  font-size: 1.4rem;
  font-weight: bolder;
  padding: 0 1rem; }

.sub_icono {
  height: 10vh; }

.sub_icono {
  height: 15vh;
  margin-right: 15px; }

.container {
  display: flex;
  flex-direction: row;
  margin-bottom: 40px; }

.text__icono {
  font-size: 2rem;
  line-height: 1.5; }

.sub__text__icono {
  font-size: 2rem;
  color: lightslategray;
  line-height: 1.15; }

@media all and (min-width: 480px) {
  .hamburguesa {
    display: none; }
  .container_menu {
    width: 60%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    list-style-type: none; }
  .item {
    color: white;
    font-weight: bold;
    text-decoration: none;
    border: 2px solid red; }
  .container_menu .item_barra::after {
    content: "|";
    margin: 1rem; }
  .main-1 {
    background-image: url("../images/premium_dancer.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right bottom;
    padding: 20px; }
  .h1 {
    font-size: 8rem;
    text-align: left;
    padding-top: 3rem; }
  .main-1_h2 {
    font-size: 2.5rem;
    text-align: left;
    padding: 2rem 0; }
  .buttom {
    display: block;
    width: 100px;
    border-radius: 2rem;
    background-color: green;
    padding: 1rem;
    text-align: center;
    margin: 1rem 0;
    color: white;
    text-decoration: none; }
  .premium {
    display: flex;
    flex-wrap: wrap; } }

@media all and (min-width: 1000px) {
  .buttom {
    font-size: 1.8rem;
    width: 25rem;
    padding: 2rem 5rem 2rem 5rem;
    border-radius: 5rem; }
  .info {
    width: 50%;
    font-size: 1.2rem;
    font-weight: 100; }
  .container {
    width: 25%;
    display: inline-block; }
  /**
* Para poder adaptar "legal", "cookies" y "Información anuncios" tendría que estar en la misma línea. ¿La mejor forma sería haber puesto en lista? 
*/ }

:root {
  font-size: 62.5%;
  font-family: spotify-circular, Circular, Helvetica, Arial, sans-serif;
  font-weight: bold; }
